window.wsft = window.wsft || {}
var https = {
     /**
     * https get
     * @param {string} url 可主动拼接参数
     * @param {(status: Number, response: String)=>{}} callback
     * @param {{}} dataInfo 也可传递对象委托参数拼接
     */
    get: function (url, callback, dataInfo) {
        if (dataInfo) {
            var connecter = '?';
            if (url[url.length - 1] === '&') {
                connecter = '';
            } else if (url.indexOf('?') >0) {
                connecter = '&';
            }
            var params = joinParams(dataInfo);
            params.length > 0 && (url += connecter + params);    
        }

        var xhr = cc.loader.getXMLHttpRequest();
        xhr.timeout = 30 * 1000;
        xhr.ontimeout = () => {
            cc.log('[tydf.https] get: request time out.');
            wsft.uiFuncs.showTip('连接超时');
        };
        xhr.onreadystatechange = () => {
            xhr.readyState === 4 && callback && callback(xhr.status, xhr.responseText);
        };
        xhr.open('GET', encodeURI(url), true);
        xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xhr.send();
    },
    
    /**
     * https post
     * @param {string} url post url
     * @param {(status: Number, response: String)=>{}} callback
     * @param {{}} dataInfo post data
     */
    post: function (url, callback, dataInfo) {
        console.log("post=="+url)
        var xhr = cc.loader.getXMLHttpRequest();
        xhr.timeout = 30 * 1000;
        xhr.ontimeout = () => {
            cc.log('wsft_http_post: request time out.');
            wsft.uiFuncs.showTip('连接超时,请检查网络!');
            wsft.loading.hide()
        };
        xhr.onreadystatechange = () => {
            xhr.readyState === 4 && callback && callback(xhr.status, xhr.responseText);
        };
        xhr.open('POST', encodeURI(url), true);
        xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
        xhr.send(joinParams(dataInfo));
    },

    postJson: function (url, callback, dataInfo) {
        var xhr = cc.loader.getXMLHttpRequest();
        xhr.onreadystatechange = () => {
            xhr.readyState === 4 && callback && callback(xhr.status, xhr.responseText);
        };
        xhr.open('POST', encodeURI(url), true);
        xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
        xhr.send(dataInfo);
    },

    /**
     * @param {string} url
     * @param {(status: Number, response: String)=>{}} callback
     */
    loadZipText: function (url, callback) {
        var xhr = cc.loader.getXMLHttpRequest();
        xhr.onreadystatechange = () => {
            xhr.readyState === 4 && callback && callback(xhr.status, xhr.responseText);
        };
        xhr.open('GET', encodeURI(url), true);
        xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
        xhr.send();
    }
}
/**
 * 连接对象成字符串参数"key1=value1&key2=value2"
 * @param {{}} dataInfo 
 */
function joinParams (dataInfo) {
    if (typeof dataInfo === 'object') {
        if (dataInfo === null) {
            return;
        } else if (Array.isArray(dataInfo)) {
            return JSON.stringify(dataInfo);
        } else {
            var keys = Object.keys(dataInfo);
            var params = keys.map((key, index) => {
                return key + '=' + dataInfo[key] + (index === keys.length - 1 ? '': '&');
            });
            return params.join('');        
        }
    } else {
        return dataInfo + '';
    }
}
wsft.https = module.exports = https;