
/**
 * 用户信息
 */
cc.Class({
    extends: cc.Component,
    properties: {
        uid:66,
        nickname:"11",//昵称
        headimg:"http://192.168.1.188/Login/functionServlet",//头像
        roomId:null,//如果有房间
        join:null,//加入的俱乐部列表
        my:null,//我的俱乐部列表
        createJ:null,//是否有权限创建俱乐部
        notice:"欢迎来到赤峰麻将",//公告
        diamond:0,
        ip:"192.168.1.1"
    },
    ctor(){
        wsft.message.addMessage(wsft.protocol.RECEIVE_LOGIN,this.socketLogin.bind(this))
        wsft.message.addMessage(wsft.protocol.PLAYER_CAST,this.playerCast.bind(this))//推送砖石
    },
    socketLogin:function(data){
        console.log("date==",data)
        if(data.code == 1){
            this.nickname = data.nickname
            this.headimg=data.headimg
            this.roomId=data.roomId
            this.notice=data.notice
            this.join=data.join
            this.my=data.my
            this.createJ=data.createJ
            if(this.roomId){
                //断线重连
                wsft.sendDataSocket.OfflineReconnection()
                wsft.loading.show("进入房间中...") 
                // wsft.uiFuncs.showTip("在房间里面房间号:"+this.roomId,30)
                // wsft.loading.hide()
            }else{
                this.jumpScene()
            } 
        }else{
            wsft.uiFuncs.showTip("个人信息获取失败",30)
            wsft.loading.hide() 
        }
    },
    playerCast:function(data){
        console.log("date==",data)
        this.diamond = data.diamond
        
    },
    jumpScene:function(){
        wsft.loading.show("正在加载资源，请稍候...") 
        cc.director.loadScene('MainScene')
    }

});


