window.wsft = window.wsft || {}
var sendDataSocket = {
    HeadrtBeat:function(){//发送心跳
        var cmd  = wsft.protocol.HEART_BEAT;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    LoginCm:function(_uid,_sessionCode,_loginTime){//登录
        var cmd  = wsft.protocol.SEND_LOGIN;
        var data = {}
        data.uid = _uid,
        data.sessionCode = _sessionCode
        data.loginTime = _loginTime
        var date = wsft.pbMsg.encodePB(cmd,data)
        // date = null
        wsft._socket.sendMsg(cmd,date)
    },
    SendPlayerRecord:function(_index){//请求战绩
        var cmd  = wsft.protocol.SEND_PLAYER_RECORD_CAST;
        var data = {}
        data.index = _index;
        var date = wsft.pbMsg.encodePB(cmd,data)
        wsft._socket.sendMsg(cmd,date)
    },
    
    CreateRoomCm:function(_playType){//创建房间
        var cmd  = wsft.protocol.SEND_CREATE_ROOM;
        var data = {}
        data.playType = _playType;
        var date = wsft.pbMsg.encodePB(cmd,data)
        // console.log(date)
        wsft._socket.sendMsg(cmd,date)
    },
    JoinRoomCm:function(_roomID){//加入房间
        var cmd  = wsft.protocol.SEND_JOIN_ROOM;
        var data = {}
        data.roomId = _roomID;
        // data.julebuRoom = "";// 暂时没有俱乐部 先写上
        var date = wsft.pbMsg.encodePB(cmd,data)
        // console.log(date)
        wsft._socket.sendMsg(cmd,date)
    },
    PrepareRoom:function(){//准备
        var cmd  = wsft.protocol.SEND_PREPARE_ROOM;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    LeaveRoom:function(){//发送退出房间
        var cmd  = wsft.protocol.SEND_LEAVE_ROOM;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    DissolveRoom:function(){//发送解散房间
        var cmd  = wsft.protocol.SEND_DISSOLVE_ROOM;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    ApplayDissolveRoom:function(_bool){//请解散房间(发送)
        var cmd  = wsft.protocol.SEND_APPLAY_DISSOLVE_ROOM;
        var data = {}
        data.agree = _bool;
        var date = wsft.pbMsg.encodePB(cmd,data)
        wsft._socket.sendMsg(cmd,date)
    },
    ChatRoom:function(_id,_type,_content){//聊天(发送)1 id 2 (1表示表情，2表示固定聊天条目，3表示自定义文字)3自定义聊天消息内容
        var cmd  = wsft.protocol.SEND_CHAT_ROOM;
        var data = {};
        data.id = _id;
        data.type = _type;
        data.content = _content;
        var date = wsft.pbMsg.encodePB(cmd,data)
        wsft._socket.sendMsg(cmd,date)
    },
    OutCard:function(_mjNum,_mjType){//出牌
        var cmd  = wsft.protocol.SEND_OUT_CARD;
        var data = {};
        data.mj = {};
        data.mj.num = _mjNum;
        data.mj.type = _mjType;
        var date = wsft.pbMsg.encodePB(cmd,data)
        wsft._socket.sendMsg(cmd,date)
        // optional Mj mj = 1;
        // optional bool shangLuo = 2;//是否选择上落
        // optional bool baoTing = 3;//是否选择报听
    },
    PengCard:function(){//碰牌
        var cmd  = wsft.protocol.SEND_PENG_CARD;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    PassCard:function(){//过牌
        var cmd  = wsft.protocol.SEND_PASS;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    JieGang:function(){//接杠
        var cmd  = wsft.protocol.SEND_JIEGANG;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    AnGang:function(_mjNum,_mjType){//暗杠
        var cmd  = wsft.protocol.SEND_ANGANG;
        var data = {};
        data.mj = {};
        data.mj.num = _mjNum;
        data.mj.type = _mjType;
        var date = wsft.pbMsg.encodePB(cmd,data)
        wsft._socket.sendMsg(cmd,date)
    },
    MingGang:function(_mjNum,_mjType){//明杠
        var cmd  = wsft.protocol.SEND_MINGGANG;
        var data = {};
        data.mj = {};
        data.mj.num = _mjNum;
        data.mj.type = _mjType;
        var date = wsft.pbMsg.encodePB(cmd,data)
        wsft._socket.sendMsg(cmd,date)
    },
    ZiMo:function(){//自摸
        var cmd  = wsft.protocol.SEND_ZI_MO;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    JiePao:function(){//接炮
        var cmd  = wsft.protocol.SEND_JIE_PAO;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },
    OfflineReconnection:function(){//断线重连
        var cmd  = wsft.protocol.SEND_OFFLINE_RECONNECTION;
        var date = null
        wsft._socket.sendMsg(cmd,date)
    },

    
    

    
    
    
    
    
}
wsft.sendDataSocket = module.exports = sendDataSocket;
