window.wsft = window.wsft || {}
var sendDataHttp = {
    Login_name_paw:function(_name,_password,_loginCallback){//账号密码登录//debug
        var data = {}
        data.type = "login"
        data.username = _name
        data.password = _password
        var url = config.loginUrl+"/Login/functionServlet"
        wsft.https.post(url,_loginCallback,data)
    },
    Login_token:function(_name,_password,_loginCallback){//toke()
        var data = {}
        data.type = "login"
        data.username = _name
        data.password = _password
        var url = config.loginUrl+"/Login/functionServlet"
        wsft.https.post(url,_loginCallback,data)
    },
    Login_wx_token:function(_name,_password,_loginCallback){//微信token
        var data = {}
        data.type = "login"
        data.username = _name
        data.password = _password
        var url = config.loginUrl+"/Login/functionServlet"
        wsft.https.post(url,_loginCallback,data)
    },
    getHeadImg:function(_url,_loginCallback){//微信token
        var data = {}
        // data.type = "login"
        // data.username = _name
        // data.password = _password
        var url = _url
        wsft.https.post(_url,_loginCallback)
    },
}
wsft.sendDataHttp = module.exports = sendDataHttp;