cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        
    },
    onEnable() {
        var scaleAction1 = cc.scaleTo(0.1, 1.25);
        var scaleAction2 = cc.scaleTo(0.1, 1);
        var seq = cc.sequence(scaleAction1, scaleAction2);
        this.node.runAction(seq);
    },
    start () {

    },

    // update (dt) {},
});
