var _copy = require("copy_h5")
cc.Class({
    extends: cc.Component,

    properties: {
        sprHeadImg:cc.Sprite,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        this.initAllBtn()
        var info_bg = this.node.getChildByName("info_bg");
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,info_bg,callback);
        // this.refreshInfo()
    },

    start () {

    },
    onEnable(){
        wsft.loading.hide()
    },
    setInfo:function(headimg,nickname,uid,ip,_isCopy){
        this.node.getChildByName("copy_btn").active = _isCopy;
        var _name = this.node.getChildByName("info_name").getComponent(cc.Label);
        var _id= this.node.getChildByName("info_id").getComponent(cc.Label);
        var _ip= this.node.getChildByName("info_ip").getComponent(cc.Label);
        var imgHead = this.sprHeadImg.node.getComponent("imgHead");
        imgHead.setHead(headimg);
        _name.string = nickname
        _id.string = "ID:"+uid
        _ip.string = "IP"+ip
        this.id = uid
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close"){
            this.node.active = false
        }else if(name === "copy"){
            var _copyStr = ""+this.id 
            if(cc.sys.isBrowser){//判断是否是浏览器
                if(_copy(_copyStr)){
                    wsft.uiFuncs.showTip("复制ID成功",32)
                }else{
                    wsft.uiFuncs.showTip("复制ID失败",32)
                }
            }else{

            }
        }

    },
   // 初始化所有的btn
   initAllBtn:function(){
        var self = this
        var btnHader = function(btn,name){
            // var btn = cc.find(btnPath)
            wsft.event.addBtnEvent(btn,self.node,'infoLayer','allBtnCallback',name);
        }
        btnHader(this.node.getChildByName("close_btn"),'close'); //close
        btnHader(this.node.getChildByName("copy_btn"),'copy'); //copy
       
   },
    // update (dt) {},
});
