cc.Class({
    // properties: {
    
    //  },
    ctor:function () {
        this._interval = 0;     // 心跳的间隔
        this._timeoutNum = 0;   // 超时的心跳包个数
        this._num = 3;          // 默认3个心跳无响应认为网络已断
        this._intervalFunc = null;
        this.HearTime = 0;//心跳延迟时间

        wsft.message.addMessage(wsft.protocol.MSG_HEART_BEAT,this.onHeartbeatBack.bind(this));
    },
    // 收到了心跳回来的消息
    onHeartbeatBack:function () {
        cc.log('onHeartbeatBack...');
        this._timeoutNum = 0;
        this.lastRecieveTime = Date.now();
        this.HearTime = this.lastRecieveTime - this.lastSendTime;
    },
     // 开始客户端心跳的逻辑
    startHeartBeat:function (interval, num) {
        //cc.log("start heartBeat!!!", interval)
        this._interval      = interval || this._interval;
        this._num           = num || this._num;
        this._timeoutNum    = 0;
        this._startTick();
    },

    _startTick:function () {
        this.stopHeartBeat();
        this._intervalFunc = setInterval(this._sendMsg.bind(this), this._interval * 1000);
        //方法可按照指定的周期（以毫秒计）来调用函数或计算表达式。
        //cc.director.getScheduler().schedule(this._sendMsg, this, this._interval, cc.macro.REPEAT_FOREVER, 0, false);
    },
    _sendMsg:function () {
        // console.log("sendHead=")
        // 检查超时
        if(++this._timeoutNum > this._num)
        {
            cc.log('Heart beat time out.');
            //this.stopHeartBeat();
            this._timeoutNum = 0;
            //请求重连
            wsft._socket.reconnect();
            return;
        }
        this.lastSendTime = Date.now();
        //发送心跳
        wsft.sendDataSocket.HeadrtBeat()
    },

    // 停止客户端心跳的逻辑
    stopHeartBeat:function () {
        if(this._intervalFunc){
            clearInterval(this._intervalFunc);
            this._intervalFunc = null;
        }
        //cc.director.getScheduler().unschedule(this._sendMsg, this);
    },

    //立刻断线重连
    startHertBeatReconnect:function(){
        this.stopHeartBeat();
        this._timeoutNum = this._num + 1;
        this._sendMsg();
    },
})