//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        
        label_1:cc.Label,
        label_2:cc.Label,
        tipType:null,
        sureBtn:null,
        cancelBtn:null,
        gameRoom:null,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.sureBtn = this.node.getChildByName("sure_btn") 
        this.cancelBtn = this.node.getChildByName("cancel_btn")
        this.initAllBtn()
    },

    start () {

    },
    setLabel:function(_type,_str1,_str2,_self){
        this.sureBtn.active = false;
        this.cancelBtn.active = false;
        this.tipType = _type;
        this.label_1.string = _str1 || "";
        this.label_2.string = _str2 || "";
        if(_type == "JieSanOK" || _type == "JieSanNoOK" ){
            this.gameRoom = _self||null
            this.sureBtn.active = true;
        }
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name == 'sure'){
            if(this.tipType == "JieSanOK"){
                this.node.active = false
                this.gameRoom.addGameOverLayer()
            }else if(this.tipType == "JieSanNoOK"){
                this.node.active = false
            }
        }
        else if(name == 'cancel'){

        }
    },
    initAllBtn:function(){
        var self = this
        var btnHader = function(btn,name){
            // var btn = cc.find(btnPath);
            wsft.event.addBtnEvent(btn,self.node,'tipLayer','allBtnCallback',name);
        }
        btnHader(this.node.getChildByName("sure_btn"),'sure'); //确定
        btnHader(this.node.getChildByName("cancel_btn"),'cancel'); //取消
    },

    // update (dt) {},
});
