    pushSendCard:function(data){//发牌（推送）800
        console.log("pushSendCard==",data)
        this.gameState = "YES"
        this.setInitCardNum()
        this.reset()
        this.seats[this.mySeatIndex - 1].handCard = data.pais;
        this.bankerSeatIndex = data.bankerSeetIndex;
        wsft.message.sendMessage(wsft.protocol.PUSHSENDCARD,data);
    },
    // optional Mj mj = 1;
	// optional bool ziMo = 2;//是否能自摸
	// repeated Mj anGang = 3;//暗杠的牌
	// repeated Mj mingGang = 4;//明杠牌
	// repeated Ting ting = 5;//打哪个牌听牌
