	// optional int32 mingPaiStatus = 8; //明牌状态   0.未明牌，1.可以明牌 2.点了明牌未打牌  3.已明牌
    catchCard:function(data){//抓牌推送804
        console.log("catchCard==",data)
        this.cardNum -= 1
        this.turnIndex = this.mySeatIndex
        var seatData = this.seats[this.mySeatIndex - 1];
        if(seatData.handCard ){
            this.seats[this.mySeatIndex - 1].handCard.push(data.mj);
        }
        if(data.anGang.length > 0){
            this.gangType = "anGang"
        }
        if(data.mingGang.length > 0){
            this.gangType = "mingGang"
        }
        if(data.ziMo){
            this.huType = "ziMo"
        }
        wsft.message.sendMessage(wsft.protocol.CATCHCARD,data);
    },
    seatCatchCard:function(data){//谁抓牌广播805
        console.log("seatCatchCard==",data)
        this.cardNum -=1
        this.turnIndex = data.seetIndex
        wsft.message.sendMessage(wsft.protocol.SEATCATCHCARD,data);
    },
    receiveOutCard:function(data){//出牌(返回)
        console.log("receiveOutCard==",data)
        this.turnIndex = this.mySeatIndex
        var seatData = this.seats[this.mySeatIndex - 1];
        if(data.code == 1){
            seatData.outOldCard.push(data.mj)
            this.outCard = data.mj;
            //移除手牌
            if(seatData.handCard){
                var idx = search(seatData.handCard,data.mj)
                if(idx != -1 ){
                    seatData.handCard.splice(idx,1);
                }else{
                    wsft.uiFuncs.showTip("手牌移除失败",30)
                }
                console.log("idxidx==",idx)
                console.log("handCardhandCard==",seatData.handCard)
            }
            //
            wsft.message.sendMessage(wsft.protocol.RECEIVEOUTCARD,data);
        }else{
            wsft.uiFuncs.showTip("出牌失败！",30)
        }
        // optional int32 code = 1;//1ok
        // optional Mj mj = 2;
        // optional bool shangLuo = 3;//是否选择上落
        // optional bool baoTing = 4;//是否选择报听
    },
    pushOutCard:function(data){//出牌(推送)
    // optional int32 seetIndex = 1;//位置
	// optional Mj mj = 2;
	// optional bool peng = 3;//是否能碰
	// optional bool jieGang = 4;//是否能接杠
	// optional bool jiePao = 5;//是否能接炮
	// repeated int32 chi = 6;//1吃小边 2吃中间 3吃大边
	// optional bool shangLuo = 7;//是否选择上落
	// optional bool baoTing = 8;//是否选择报听
        console.log("pushOutCard==",data)
        this.turnIndex = data.seetIndex
        var seatData = this.seats[data.seetIndex - 1];
        if (this.mySeatIndex == data.seetIndex){
            if(seatData.handCard){
                var idx = search(seatData.handCard,data.mj)
                if(idx != -1 ){
                    seatData.handCard.splice(idx,1);
                }else{
                    wsft.uiFuncs.showTip("手牌移除失败",30)
                }
            }
        }
        seatData.outOldCard.push(data.mj)
        this.outCard = data.mj;
        if(data.jieGang){
            this.gangType = "jieGang"
        }
        if(data.jiePao){
            this.huType = "jiePao"
        }
        wsft.message.sendMessage(wsft.protocol.PUSHOUTCARD,data);
    },
    receivePassCard:function(data){//过牌(返回)
        console.log("receivePassCard==",data)
        if(data.code == 1){
            wsft.message.sendMessage(wsft.protocol.RECEIVEPASS,data);
        }else{
            wsft.uiFuncs.showTip("过牌失败",30)
        }
    },
    receivePengCard:function(data){//碰牌(返回)
        // optional int32 code = 1;//1ok
	    // optional Mj mj = 2;
	    // optional int32 daSeetIndex = 3;//谁出的
        console.log("receivePengCard==",data)
        if(data.code == 1){
            wsft.message.sendMessage(wsft.protocol.RECEIVEPENGCARD,data);
        }else{
            wsft.uiFuncs.showTip("碰牌失败",30)
        }
    },
    pushPengCard:function(data){//碰牌(推送)
        // optional int32 seetIndex = 1;//位置
        // optional Mj mj = 2;
        // repeated Mj anGang = 3;//暗杠的牌
        // repeated Mj mingGang = 4;//明杠牌
        // repeated Ting ting = 5;//打哪个牌听牌
        // optional int32 daSeetIndex = 6;//谁出的
        // optional int32 mingPaiStatus = 7; //明牌状态   0.未明牌，1.可以明牌 2.点了明牌未打牌  3.已明牌
        console.log("pushPengCard==",data)
        this.turnIndex = data.seetIndex
        //移除出过的牌
        this.seats[data.daSeetIndex - 1].outOldCard.pop()
        //添加碰
        this.seats[data.seetIndex - 1].pengs.push(data.mj)
        //
        if(data.seetIndex == this.mySeatIndex){
            var seatData = this.seats[this.mySeatIndex - 1];
            //移除手牌
            if(seatData.handCard){
                for(var i = 0; i < 2; ++i){
                    var idx = search(seatData.handCard,data.mj)
                    if(idx != -1 ){
                        seatData.handCard.splice(idx,1);
                    }else{
                        wsft.uiFuncs.showTip("手牌移除失败",30)
                    }
                }               
            }
        }
        wsft.message.sendMessage(wsft.protocol.PUSHPENGCARD,data);
    },
    receiveAnGang:function(data){//暗杠(返回)
        console.log("receiveAnGang==",data)
        if(data.code == 1){
            this.turnIndex = this.mySeatIndex
            //添加暗杠
            this.seats[this.mySeatIndex - 1].anGangs.push(data.mj)

            var seatData = this.seats[this.mySeatIndex - 1];
            //移除手牌
            if(seatData.handCard){
                for(var i = 0; i < 4; ++i){
                    var idx = search(seatData.handCard,data.mj)
                    if(idx != -1 ){
                        seatData.handCard.splice(idx,1);
                    }else{
                        wsft.uiFuncs.showTip("手牌移除失败",30)
                    }
                }               
            }
            wsft.message.sendMessage(wsft.protocol.RECEIVEANGANG,data);
        }else{
            wsft.uiFuncs.showTip("暗杠失败",30)
        }
    },
    pushAnGang:function(data){//暗杠(推送)
        console.log("pushAnGang==",data)
        if(data.seetIndex != this.mySeatIndex){
            
            this.turnIndex = data.seetIndex
            //添加暗杠
            this.seats[data.seetIndex - 1].anGangs.push(data.mj) 

            wsft.message.sendMessage(wsft.protocol.PUSHANGANG,data);
        }
        
    },
