    pushMingGang:function(data){//明杠(推送)
        console.log("pushMingGang==",data)
        this.turnIndex = data.seetIndex
        var seatData = this.seats[data.seetIndex - 1];
        if(seatData.pengs){
            var idx = search(seatData.pengs,data.mj)
            if(idx != -1 ){
                seatData.pengs.splice(idx,1);
            }else{
                wsft.uiFuncs.showTip("碰牌移除失败",30)
            }              
        }
        //添加明杠
        this.seats[data.seetIndex - 1].mingGangs.push(data.mj)
        if(data.seetIndex == this.mySeatIndex){
            //移除手牌
            if(seatData.handCard){
                var idx = search(seatData.handCard,data.mj)
                if(idx != -1 ){
                    seatData.handCard.splice(idx,1);
                }else{
                    wsft.uiFuncs.showTip("手牌移除失败",30)
                }             
            }
        }
        wsft.message.sendMessage(wsft.protocol.PUSHMINGGANG,data);
    },
    receiveJieGang:function(data){//接杠(返回)
        console.log("receiveJieGang==",data)
        if(data.code == 1){
            wsft.message.sendMessage(wsft.protocol.RECEIVEJIEGANG,data);
        }else{
            wsft.uiFuncs.showTip("接杠失败",30)
        }
    },
    pushJieGang:function(data){//接杠(推送)
        console.log("pushJieGang==",data)
        this.turnIndex = data.seetIndex
        //移除出过的牌
        this.seats[data.daSeetIndex - 1].outOldCard.pop()
        //添加接杠
        this.seats[data.seetIndex - 1].jieGangs.push(data.mj)

        if(data.seetIndex == this.mySeatIndex){
            var seatData = this.seats[this.mySeatIndex - 1];
            //移除手牌
            if(seatData.handCard){
                for(var i = 0; i < 3; ++i){
                    var idx = search(seatData.handCard,data.mj)
                    if(idx != -1 ){
                        seatData.handCard.splice(idx,1);
                    }else{
                        wsft.uiFuncs.showTip("手牌移除失败",30)
                    }
                }               
            } 
        }
        wsft.message.sendMessage(wsft.protocol.PUSHJIEGANG,data);
    },
    receiveZiMo:function(data){//自摸(返回)
        console.log("receiveZiMo==",data)
        if(data.code == 1){
            wsft.message.sendMessage(wsft.protocol.RECEIVEZIMO,data);
        }else{
            wsft.uiFuncs.showTip("自摸失败",30)
        }
    },
    receiveJiePao:function(data){//接炮(返回)
        console.log("receiveJiePao==",data)
        if(data.code == 1){
            wsft.message.sendMessage(wsft.protocol.RECEIVEJIEPAO,data);
        }else{
            wsft.uiFuncs.showTip("接炮失败",30)
        }

    },
    receiveOfflineReconnection:function(data){//断线重连(返回)
        console.log("receiveOfflineReconnection==",data) 
        if (data.code == 1){
            this.roomId = data.roomId;//房间号
            this.rule = data.playType;//玩法
            this.playerNum = data.playType.num;//几人玩法
            this.seats = data.users;
            this.setRuleLocalStorage(data.playType)
            for(var i = 0;i< data.users.length;++i){
                if(wsft.userData.uid == data.users[i].uid ){
                    this.mySeatIndex = data.users[i].seetIndex
                }
            }
            if (data.start == false){
                this.gameState = "NO"
                this.jumpGameScene()
            }else{
                this.gameState = "YES"
                this.reset()
                //2,5,8,11,14
                if(data.my){
                    this.cardNum = data.my.leftPaiNum;
                    this.seats[this.mySeatIndex - 1].handCard =wsft.funcs.deepCopy(data.my.pais);
                }
                this.bankerSeatIndex = data.bankerSeetIndex
                // type = 1;//1暗杠 2明杠 3接杠 4碰 5吃
                for(var i in data.usersRound){
                    var seatData = this.seats[data.usersRound[i].seetIndex - 1];
                    // this.seats[data.usersRound[i].seetIndex - 1].outOldCard = data.usersRound[i].mj
                    for(var j = 0;j < data.usersRound[i].mj.length ;++j){//出过的牌
                        seatData.outOldCard.push(data.usersRound[i].mj[j])
                    }
                    var gpc = data.usersRound[i].gpc
                    for(var g = 0;g < gpc.length;++g){//碰 杠
                        var type = gpc[g].type
                        // console.log("typetype==",type)
                        if(type == 1){
                            seatData.anGangs.push(gpc[g].mj);
                        }else if(type == 2){
                            seatData.mingGangs.push(gpc[g].mj);
                        }else if(type == 3){
                            seatData.jieGangs.push(gpc[g].mj);
                        }else if(type == 4){
                            seatData.pengs.push(gpc[g].mj);
                        }
                    }
                }
                this.jumpGameScene()
                wsft.message.sendMessage(wsft.protocol.RECEIVEOFFLINERECONNECTION,data);
            }
        }else{

        }
    },
    pushRoundResult:function(data){//牌局结果 825
        console.log("pushRoundResult==",data)
        for(var i in data.rs){
            var seatData = this.seats[data.rs[i].seetIndex - 1];
            seatData.handCard =  wsft.funcs.deepCopy(data.rs[i].mj)
            if(data.reconnect){
                var gpc = data.rs[i].gpc
                for(var g = 0;g < gpc.length;++g){//碰 杠
                    var type = gpc[g].type
                    // console.log("typetype==",type)
                    if(type == 1){
                        seatData.anGangs.push(gpc[g].mj);
                    }else if(type == 2){
                        seatData.mingGangs.push(gpc[g].mj);
                    }else if(type == 3){
                        seatData.jieGangs.push(gpc[g].mj);
                    }else if(type == 4){
                        seatData.pengs.push(gpc[g].mj);
                    }
                } 
            }else{
            }
            
        }
        wsft.message.sendMessage(wsft.protocol.PUSHROUNDRESULT,data);
    },
