var pbCore = require("pbCore")
cc.Class({
    ctor:function(){
        this.sendMsg = [],//发送
        this.receiveMsg =[],//接受
        pbCore.preload(() => {
            this.pb();
        });
    },
    pb:function(){
        var loginpb = pbCore.loadFromFile('loginMessage.proto', 'msg.LoginMessage');
        this.sendMsg[wsft.protocol.SEND_LOGIN] = loginpb.LoginCm;
        this.receiveMsg[wsft.protocol.RECEIVE_LOGIN] = loginpb.LoginSm;
        this.receiveMsg[wsft.protocol.PLAYER_CAST] = loginpb.PlayerCast;
        this.receiveMsg[wsft.protocol.PUSH_PLAYER_RECORD_CAST] = loginpb.PlayerRecordCast;//记录推送
        this.sendMsg[wsft.protocol.SEND_PLAYER_RECORD_CAST] = loginpb.GameRecordCm;//记录请求
        this.receiveMsg[wsft.protocol.RECEIVE_PLAYER_RECORD_CAST] = loginpb.GameRecordSm;//记录返回
        
        var roompb = pbCore.loadFromFile('roomMessage.proto', 'msg.RoomMessage');
        this.sendMsg[wsft.protocol.SEND_CREATE_ROOM] = roompb.CreateRoomCm;//创建房间fa
        this.receiveMsg[wsft.protocol.RECEIVE_CREATE_ROOM] = roompb.CreateRoomSm;//创建房间
        
        this.sendMsg[wsft.protocol.SEND_JOIN_ROOM] = roompb.JoinRoomCm;//发送加入房间
        this.receiveMsg[wsft.protocol.RECEIVE_JOIN_ROOM] = roompb.JoinRoomSm;//接受加入房间
        this.receiveMsg[wsft.protocol.PUSH_JOIN_ROOM] = roompb.JoinRoomCast;//推送加入房间

        
        this.sendMsg[wsft.protocol.SEND_PREPARE_ROOM] = roompb.PrepareRoomCm;//准备(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_PREPARE_ROOM] = roompb.PrepareRoomSm;//准备(返回)
        this.receiveMsg[wsft.protocol.PUSH_PREPARE_ROOM] = roompb.PrepareRoomCast;//准备(推送)
        
        this.receiveMsg[wsft.protocol.RECEIVE_LEAVE_ROOM] = roompb.LeaveRoomSm;//离开房间返回
        this.receiveMsg[wsft.protocol.RECEIVE_DISSOLVE_ROOM] = roompb.DissolveRoomSm;//解散房间(返回)
        this.receiveMsg[wsft.protocol.PUSH_DISSOLVE_ROOM] = roompb.DissolveRoomCast;//解散房间(推送)

        this.sendMsg[wsft.protocol.SEND_APPLAY_DISSOLVE_ROOM] = roompb.MemberDissolveRoomCm;//申请解散房间(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_APPLAY_DISSOLVE_ROOM] = roompb.MemberDissolveRoomSm;//申请解散房间(返回)
        this.receiveMsg[wsft.protocol.PUSH_APPLAY_DISSOLVE_ROOM] = roompb.MemberDissolveRoomCast;//申请解散房间(推送)
        
        this.sendMsg[wsft.protocol.SEND_CHAT_ROOM] = roompb.ChatCm;//聊天(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_CHAT_ROOM] = roompb.ChatSm;//聊天(返回)
        this.receiveMsg[wsft.protocol.PUSH_CHAT_ROOM] = roompb.ChatCast;//聊天(推送)
        
        
        this.receiveMsg[wsft.protocol.PUSH_OFFLINE] = roompb.OfflineCast;//断线广播
        var mjpb = pbCore.loadFromFile('mjMessage.proto', 'msg.MjMessage');

        this.receiveMsg[wsft.protocol.PUSH_SEND_CARD] = mjpb.FaPaiCast;//发牌(推送)
        this.receiveMsg[wsft.protocol.CATCH_CARD] = mjpb.ZhuaPaiCast;//抓牌推送
        this.receiveMsg[wsft.protocol.SEAT_CATCH_CARD] = mjpb.SeetZhuaPaiCast;//谁抓牌广播

        this.sendMsg[wsft.protocol.SEND_OUT_CARD] = mjpb.DaPaiCm;//出牌(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_OUT_CARD] = mjpb.DaPaiSm;//出牌(返回)
        this.receiveMsg[wsft.protocol.PUSH_OUT_CARD] = mjpb.DaPaiCast;//出牌(推送)

        this.sendMsg[wsft.protocol.SEND_PASS] = mjpb.OverCm;//过(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_PASS] = mjpb.OverSm;//过(返回)
        

        this.sendMsg[wsft.protocol.SEND_ANGANG] = mjpb.AnGangPaiCm;//暗杠(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_ANGANG] = mjpb.AnGangPaiSm;//暗杠(返回)
        this.receiveMsg[wsft.protocol.PUSH_ANGANG] = mjpb.AnGangPaiCast;//暗杠(推送)

        this.sendMsg[wsft.protocol.SEND_MINGGANG] = mjpb.MingGangPaiCm;//明杠(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_MINGGANG] = mjpb.MingGangPaiSm;//明杠(返回)
        this.receiveMsg[wsft.protocol.PUSH_MINGGANG] = mjpb.MingGangPaiCast;//明杠(推送)

        this.sendMsg[wsft.protocol.SEND_JIEGANG] = mjpb.JieGangCm;//接杠(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_JIEGANG] = mjpb.JieGangSm;//接杠(返回)
        this.receiveMsg[wsft.protocol.PUSH_JIEGANG] = mjpb.JieGangCast;//接杠(推送)

        this.sendMsg[wsft.protocol.SEND_PENG_CARD] = mjpb.PengPaiCm;//碰牌(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_PENG_CARD] = mjpb.PengPaiSm;//碰牌(返回)
        this.receiveMsg[wsft.protocol.PUSH_PENG_CARD] = mjpb.PengPaiCast;//碰牌(推送)

        this.sendMsg[wsft.protocol.SEND_ZI_MO] = mjpb.ZiMoPaiCm;//自摸(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_ZI_MO] = mjpb.ZiMoPaiSm;//自摸(返回)

        this.sendMsg[wsft.protocol.SEND_JIE_PAO] = mjpb.JiePaoCm;//接炮(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_JIE_PAO] = mjpb.JiePaoSm;//接炮(返回)

        this.sendMsg[wsft.protocol.SEND_OFFLINE_RECONNECTION] = mjpb.ReconnectCm;//断线重连(发送)
        this.receiveMsg[wsft.protocol.RECEIVE_OFFLINE_RECONNECTION] = mjpb.ReconnectSm;//断线重连(返回)
        

        

        
        
        this.receiveMsg[wsft.protocol.PUSH_ROUND_RESULT] = mjpb.RoundResultCast;//牌局结果
        this.receiveMsg[wsft.protocol.PUSH_GAME_OVER] = mjpb.GameOverCast;//游戏结束


        
        
    },
    //发送
    encodePB:function(msgId,msgTab){
        var self = this;
        var pbData = self.sendMsg[msgId];
        if(pbData == null){
            cc.error("encodePB=="+msgId+"PB is Null");
        }
        return pbData.encode(msgTab).toArrayBuffer();
    },
    //接受
    decodePB:function(msgId,msgTab){
        var self = this;
        var pbData = self.receiveMsg[msgId];
        if(pbData == null){
            cc.error("encodePB=="+msgId+"PB is Null");
        }
        return pbData.decode(msgTab);
    },

});
