
var mjSprite = []
cc.Class({
    extends: cc.Component,

    properties: {
        upAtlas:cc.SpriteAtlas,//上边的plist图片
        leftAtlas:cc.SpriteAtlas,//左边的plist图片
        rightAtlas:cc.SpriteAtlas,//右边的plist图片
        bottomAtlas:cc.SpriteAtlas,//对面的plist图片
        myAtlas:cc.SpriteAtlas,//我的plist图片
        emptyAtlas:cc.SpriteAtlas,//空
        chatAtlas:cc.SpriteAtlas,//聊天
        pengGangMyUpPre:cc.Prefab,//自己 上的碰杠的牌
        pengGangLeftRightPre:cc.Prefab,//左右 碰杠的牌
        myOutOldCardPre:cc.Prefab,//出的牌
        rightOutOldCardPre:cc.Prefab,
        upOutOldCardPre:cc.Prefab,
        leftOutOldCardPre:cc.Prefab,
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        wsft.mjCard = this
        this._sides = ['myself_node','right_node','up_node','left_node'];
        this._pres = ["M_","R_","U_","L_"]
        this._outPres = ["B_","R_","U_","L_"];
        // //筒
        // for(var i = 1;i<10;++i){
        //     mjSprite.push("B_"+i)
        // }
        // //条
        // for(var i = 1;i<10;++i){
        //     mjSprite.push("T_"+i)
        // }
        // //万
        // for(var i = 1;i<10;++i){
        //     mjSprite.push("W_"+i)
        // }
        // //中发白
        // mjSprite.push("C_zhong")
        // mjSprite.push("C_fa")
        // mjSprite.push("C_bai")
        // //东南西北
        // mjSprite.push("C_dong")
        // mjSprite.push("C_nan")
        // mjSprite.push("C_xi")
        // mjSprite.push("C_bei")

    },
    getMJSpriteByID:function(id){
        return mjSprite[id];
    },
    getMJType:function(id){
        if(id >= 0 && id < 9){
            return 0;
        }
        else if(id >= 9 && id < 18){
            return 1;
        }
        else if(id >= 18 && id < 27){
            return 2;
        }
    },
    getChatSpriteFrame:function(_index){
        var spriteFrameName = "biaoqing_"+_index
        return this.chatAtlas.getSpriteFrame(spriteFrameName); 
    },
    getSpriteFrameByMJ:function(pre,mjNum,mjType){
        // console.log("getSpriteFrameByMJ======")
        var spriteFrameName = ""
        if(mjType == 1){//万
            spriteFrameName = "W_"+mjNum
        }else if(mjType == 2){//条
            spriteFrameName = "T_"+mjNum
        }else if(mjType == 3){//筒
            spriteFrameName = "B_"+mjNum
        }else if(mjType == 4){//风
            spriteFrameName = "F_"+mjNum
        }
        spriteFrameName = pre + spriteFrameName;
        if(pre == "M_"){
            return this.myAtlas.getSpriteFrame(spriteFrameName);            
        }
        else if(pre == "B_"){
            return this.bottomAtlas.getSpriteFrame(spriteFrameName);
        }
        else if(pre == "U_"){
            return this.upAtlas.getSpriteFrame(spriteFrameName);
        }
        else if(pre == "L_"){
            return this.leftAtlas.getSpriteFrame(spriteFrameName);
        }
        else if(pre == "R_"){
            return this.rightAtlas.getSpriteFrame(spriteFrameName);
        }
        
    },
    getEmptySpriteFrame:function(side){
        if(side == "up_node"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_up");
        }   
        else if(side == "myself_node"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_up");
        }
        else if(side == "left_node"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_left");
        }
        else if(side == "right_node"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_right");
        }

    },
    getEmptySpriteFrameHand:function(side){
        if(side == "up_node"){
            return this.emptyAtlas.getSpriteFrame("e_mj_up");
        }  
        else if(side == "left_node"){
            return this.emptyAtlas.getSpriteFrame("e_mj_left");
        }
        else if(side == "right_node"){
            return this.emptyAtlas.getSpriteFrame("e_mj_right");
        }

    },
    getAudioURLByMJID:function(id){
        // var realId = 0;
        // if(id >= 0 && id < 9){
        //     realId = id + 21;
        // }
        // else if(id >= 9 && id < 18){
        //     realId = id - 8;
        // }
        // else if(id >= 18 && id < 27){
        //     realId = id - 7;
        // }
        // return "nv/" + realId + ".mp3";
    },
    getSide:function(localIndex){
        return this._sides[localIndex];
    },
    
    getPre:function(localIndex){
        return this._pres[localIndex];
    },
    getOutPre:function(localIndex){
        return this._outPres[localIndex];
    },
    start () {

    },
    // update (dt) {},
});
