cc.Class({
    extends: cc.Component,

    properties: {
        _outCards:[],
        pointNode:[],
    },

    // LIFE-CYCLE CALLBACKS:
    onLoad () {
        console.log("outOldCards======")
        this.initView()
        this.addInitPoint()
        
        this.addMessgetListen()
    },

    start () {
        
    },
    addMessgetListen:function(){
        wsft.message.addMessage(wsft.protocol.CATCHCARD,this.catchCard.bind(this))//摸牌
        wsft.message.addMessage(wsft.protocol.RECEIVEOUTCARD,this.receiveOutCards.bind(this))//出牌(返回)
        wsft.message.addMessage(wsft.protocol.PUSHOUTCARD,this.pushOutCards.bind(this))//出牌(推送)
        // wsft.message.addMessage(wsft.protocol.RECEIVEPENGCARD,this.receivePengCards.bind(this))//碰牌(返回)
        wsft.message.addMessage(wsft.protocol.PUSHPENGCARD,this.pushPengCards.bind(this))//碰牌(推送)
        wsft.message.addMessage(wsft.protocol.RECEIVEOFFLINERECONNECTION,this.receiveOfflineReconnections.bind(this))//断线重连
        wsft.message.addMessage(wsft.protocol.PUSHROUNDRESULT,this.pushRoundResultoc.bind(this))//牌局结果 小结算    
    },
    pushRoundResultoc:function(data){//牌局结果 小结算
        var self = this;
        if(data.reconnect){
            self.hideAllOutCards()
            self.hideAllPoint()
        }else{
            setTimeout(function(){
                self.hideAllOutCards()
                self.hideAllPoint()
            },3000)
        }
        
    },
    receiveOfflineReconnections:function(){//断线重连
        // console.log("outOldCard===DXCL=")
        this.initAllOldCard()
    },
    receivePengCards:function(data){//碰牌(返回)
        // console.log("碰牌返回")
        // var index = data.daSeetIndex - 1;
        // var seats = wsft.gameData.seats;
        // this.initOutOldCard(seats[index],false);
    },
    pushPengCards:function(data){//碰牌(推送)
        console.log("碰牌推送")
        var index = data.daSeetIndex - 1;
        var seats = wsft.gameData.seats;
        this.initOutOldCard(seats[index],false);
    },

    receiveOutCards:function(data){//出牌(返回)
        console.log("出牌返回")
        var self = this
        setTimeout(function(){
            var index = wsft.gameData.mySeatIndex - 1;
            var seats = wsft.gameData.seats;
            self.initOutOldCard(seats[index],true);
        },500)
        
    },
    pushOutCards:function(data){//出牌(推送)
        console.log("出牌推送")
        var self = this
        setTimeout(function(){
            var index = data.seetIndex - 1;
            var seats = wsft.gameData.seats;
            self.initOutOldCard(seats[index],true);
        },500)
    },
    catchCard:function(data){//摸牌
        
    },
    initView:function(){
        // this._outCards = [];
        if(wsft.gameData.playerNum  == 4){
            this.initOutCardFour()
        }else if(wsft.gameData.playerNum  == 3){
            this.initOutCardThree()
        }
        else if(wsft.gameData.playerNum  == 2){
            this.initOutCardTwo()
        }
        this.hideAllOutCards();
    },
    initOutCardFour:function(){//4人
        var gameNode = this.node.getChildByName("game_node");
        var sides = ['myself_node','right_node','up_node','left_node']
        for(var s = 0;s < 4;++s){
            var sideName = sides[s];
            var sideRoot = gameNode.getChildByName(sideName);
            var outOldCardPra = sideRoot.getChildByName("out_old_card");
            var _oldCrads = [];
            for(var i = 0; i < 22; ++i){
                var out_old_card = null;
                if(s == 0){
                    out_old_card = cc.instantiate(wsft.mjCard.myOutOldCardPre);
                }else if(s == 1){
                    out_old_card = cc.instantiate(wsft.mjCard.rightOutOldCardPre);
                }else if(s == 2){
                    out_old_card = cc.instantiate(wsft.mjCard.upOutOldCardPre);
                }else if(s == 3){
                    out_old_card = cc.instantiate(wsft.mjCard.leftOutOldCardPre);
                }
                // console.log(out_old_card)
                if(i<11){
                    if(sideName == "myself_node"){
                        out_old_card.x = i*55 -(55*5)
                        out_old_card.y = 128
                    }else if(sideName == "right_node"){
                        out_old_card.zIndex = 33 - i
                        out_old_card.x = 0
                        out_old_card.y = 25*i-(25*5) 
                    }else if(sideName == "up_node"){
                        out_old_card.zIndex = 3
                        out_old_card.x = -i*55+(55*5)
                        out_old_card.y = -128
                    }else if(sideName == "left_node"){
                        out_old_card.x = 0
                        out_old_card.y = -25*i+(25*5)
                    }
                    
                }else{
                    if(sideName == "myself_node"){
                        out_old_card.x = (i-11)*55-(55*5)
                        out_old_card.y = 64
                    }else if(sideName == "right_node"){
                        out_old_card.zIndex = 33 - i
                        out_old_card.x = 48
                        out_old_card.y = 25*(i-11)-(25*5) 
                    }else if(sideName == "up_node"){
                        out_old_card.zIndex = 2
                        out_old_card.x = -(i-11)*55+(55*5)
                        out_old_card.y = -64
                    }else if(sideName == "left_node"){
                        out_old_card.x = -47
                        out_old_card.y = -25*(i-11)+(25*5) 
                    }
                }
                outOldCardPra.addChild(out_old_card);
                out_old_card.active = false
                var sprite = out_old_card.getComponent(cc.Sprite); 
                sprite.spriteFrame = null;
                _oldCrads.push(sprite);     
            }
            this._outCards[sideName] = _oldCrads;
        }
    },
    initOutCardThree:function(){//3人
        var gameNode = this.node.getChildByName("game_node");
        var sides = ['myself_node','right_node','left_node']
        for(var s = 0;s < 3;++s){
            var sideName = sides[s];
            var sideRoot = gameNode.getChildByName(sideName);
            var outOldCardPra = sideRoot.getChildByName("out_old_card");
            var _oldCrads = [];
            for(var i = 0; i < 33; ++i){
                var out_old_card = null;
                if(s == 0){
                    out_old_card = cc.instantiate(wsft.mjCard.myOutOldCardPre);
                }else if(s == 1){
                    out_old_card = cc.instantiate(wsft.mjCard.rightOutOldCardPre);
                }else if(s == 2){
                    out_old_card = cc.instantiate(wsft.mjCard.leftOutOldCardPre);
                }
                // console.log(out_old_card)
                if(i<11){
                    if(sideName == "myself_node"){
                        out_old_card.x = i*55 -(55*5)
                        out_old_card.y = 128
                    }else if(sideName == "right_node"){
                        out_old_card.zIndex = 33 - i
                        out_old_card.x = 0
                        out_old_card.y = 25*i-(25*5) 
                    }else if(sideName == "left_node"){
                        out_old_card.x = 0
                        out_old_card.y = -25*i+(25*5)
                    }
                    
                }else if(i>=11 && i< 22){
                    if(sideName == "myself_node"){
                        out_old_card.x = (i-11)*55-(55*5)
                        out_old_card.y = 64
                    }else if(sideName == "right_node"){
                        out_old_card.zIndex = 33 - i
                        out_old_card.x = 48
                        out_old_card.y = 25*(i-11)-(25*5) 
                    }else if(sideName == "left_node"){
                        out_old_card.x = -47
                        out_old_card.y = -25*(i-11)+(25*5) 
                    }
                }else{
                    if(sideName == "myself_node"){
                        out_old_card.x = (i-22)*55-(55*5)
                        out_old_card.y = 0
                    }
                    else if(sideName == "right_node"){
                        out_old_card.zIndex = 33 - i
                        out_old_card.x = 96
                        out_old_card.y = 25*(i-22)-(25*5) 
                    }
                    else if(sideName == "left_node"){
                        out_old_card.x = -94
                        out_old_card.y = -25*(i-22)+(25*5) 
                    }
                }
                outOldCardPra.addChild(out_old_card);
                out_old_card.active = false
                var sprite = out_old_card.getComponent(cc.Sprite); 
                sprite.spriteFrame = null;
                _oldCrads.push(sprite);     
            }
            this._outCards[sideName] = _oldCrads;
        }
    },
    initOutCardTwo:function(){//2人
        var gameNode = this.node.getChildByName("game_node");
        var sides = ['myself_node','up_node']
        for(var s = 0;s < 2;++s){
            var sideName = sides[s];
            var sideRoot = gameNode.getChildByName(sideName);
            var outOldCardPra = sideRoot.getChildByName("out_old_card");
            var _oldCrads = [];
            for(var i = 0; i < 56; ++i){
                var out_old_card = null;
                if(s == 0){
                    out_old_card = cc.instantiate(wsft.mjCard.myOutOldCardPre);
                }else if(s == 1){
                    out_old_card = cc.instantiate(wsft.mjCard.upOutOldCardPre);
                }
                // console.log(out_old_card)
                if(i<20){
                    if(sideName == "myself_node"){
                        out_old_card.x = i*55 -(55*10-27.5)
                        out_old_card.y = 128
                    }else if(sideName == "up_node"){
                        out_old_card.zIndex = 3
                        out_old_card.x = -i*55+(55*10-27.5)
                        out_old_card.y = -128
                    }
                    
                }else if(i>=20 && i< 40){
                    if(sideName == "myself_node"){
                        out_old_card.x = (i-20)*55-(55*10-27.5)
                        out_old_card.y = 64
                    }else if(sideName == "up_node"){
                        out_old_card.zIndex = 2
                        out_old_card.x = -(i-20)*55+(55*10-27.5)
                        out_old_card.y = -64
                    }
                }else{
                    if(sideName == "myself_node"){
                        out_old_card.x = (i-40)*55-(55*10-27.5)
                        out_old_card.y = 0
                    }else if(sideName == "up_node"){
                        out_old_card.zIndex = 1
                        out_old_card.x = -(i-40)*55+(55*10-27.5)
                        out_old_card.y = 0
                    }
                }
                outOldCardPra.addChild(out_old_card);
                out_old_card.active = false
                var sprite = out_old_card.getComponent(cc.Sprite); 
                sprite.spriteFrame = null;
                _oldCrads.push(sprite);     
            }
            this._outCards[sideName] = _oldCrads;
        }
    },
    initAllOldCard:function(){
        var seats = wsft.gameData.seats;
        for(var i in seats){
            this.initOutOldCard(seats[i],false);
        }
    },
    hideAllOutCards:function(){
        for(var k in this._outCards){
            var f = this._outCards[k];
            for(var i in f){
                f[i].node.active = false;
            }
        }
    },
    initOutOldCard:function(seatData,_boolPoint){
        var outOldCard = seatData.outOldCard;
        // console.log("outOldCard==",outOldCard);
        if(outOldCard == null){
            return;
        }
        var index =  wsft.gameData.getLocalIndex(seatData.seetIndex);
        var _side = wsft.mjCard.getSide(index);
        var _pre = wsft.mjCard.getPre(index);
        if(_pre =="M_"){
            _pre = "B_";
        }
        // console.log("outOldCard3==",outOldCard);
        var outOldCardSprites = this._outCards[_side];
        // console.log("outOldCardSprites==",outOldCardSprites);
        for(var i = 0; i < outOldCardSprites.length; ++i){
            
            var sprite = outOldCardSprites[i];
            sprite.node.active = true;
            if(i < outOldCard.length ){
                this.setSpriteFrameByMJ(_pre,sprite,outOldCard[i].num,outOldCard[i].type);
            }
        }
        
        for(var i = outOldCard.length; i < outOldCardSprites.length; ++i){
            var sprite = outOldCardSprites[i];
            sprite.node.active = false;
            sprite.spriteFrame = null;
        } 
        // outOldCardSprites
        // console.log("outOldCard2==",outOldCard);
        if(_boolPoint){
            var _sprite = outOldCardSprites[outOldCard.length -1];
            this.movePointAct(index,_sprite)
        }else{
            this.hideAllPoint()
        }
    },
    addInitPoint:function(){
        var gameNode = this.node.getChildByName("game_node");
        var sides = ['myself_node','right_node','up_node','left_node']
        for(var i = 0;i < sides.length; ++i ){
            var sideName = sides[i];
            var sideRoot = gameNode.getChildByName(sideName);
            var outOldParent = sideRoot.getChildByName("out_old_card_point");
            outOldParent.active = true
            this.pointNode[i] = outOldParent.getChildByName("point")
            this.pointNode[i].active = false
            // this.upDownAni(i)
        }
    },
    movePointAct:function(index,_sprite){
        for(var i = 0;i < 4 ; ++i){
            if(index === i){
                // var x = 0
                var y = 0
                if(index == 0){
                    y = _sprite.node.y + 32;
                }else if(index == 1 || index == 3){
                    y = _sprite.node.y + 25;
                }else if(index == 2){
                    y =_sprite.node.y + 25;
                }
                this.pointNode[index].active = true
                this.pointNode[index].x = _sprite.node.x
                this.pointNode[index].y = y
                this.upDownAni(this.pointNode[index],this.pointNode[index].x,this.pointNode[index].y)
            }else{
                this.pointNode[i].active = false
                this.stopAni(this.pointNode[i])
            }
            
        }
    },
    hideAllPoint:function(){
        for(var i = 0;i < 4 ; ++i){
            this.pointNode[i].active = false
            this.stopAni(this.pointNode[i])
        }
    },
    //上下摆动
    upDownAni:function(_pointNode,x,y){
        var ac = cc.sequence(
            cc.moveTo(0.3, x,y + 15),
            cc.delayTime(0.1),
            cc.moveTo(0.3, x,y),
            cc.delayTime(0.1),
        )
        var forever = cc.repeatForever(ac);
        _pointNode.runAction(forever)
    },
    stopAni:function(_node){
        _node.stopAllActions();
    },
    setSpriteFrameByMJ:function(pre,sprite,mjNum,mjType){
        sprite.spriteFrame = wsft.mjCard.getSpriteFrameByMJ(pre,mjNum,mjType);
        sprite.node.active = true;
    },
    // update (dt) {},
});
