
cc.Class({
    extends: cc.Component,

    properties: {
        agreementLayerPrafab:cc.Prefab,
        uid:null,
        sessionCode:null,
        loginTime:null,
    },

    onLoad () {
        wsft.gameData.gameSceneName = "LoginScene"
        wsft.funcs.removeGameMessage()
        wsft.funcs.removeMainMessage()
    },
    start () {
        var self = this
        //初始化所有btn事件
        this.initAllBtn()
        //获取复选框
        this.agreementCheckBox = cc.find("Canvas/agreement_checkBox").getComponent(cc.Toggle);
        wsft.message.addMessage(wsft.protocol.TCP_ONOPEN,this.tcpOnOpen.bind(this))//监听链接成功
        
    },
    tcpOnOpen:function(){//链接服务器成功
        console.log("连接成功")
        wsft.heartBeat.startHeartBeat(5,3)//链接心跳
        wsft.sendDataSocket.LoginCm(this.uid,this.sessionCode,this.loginTime)//登录
    },
    loginCallback:function(status,response){
        console.log("========"+response)
        console.log("========"+status)
        var self = this
        if(status == 200){
            var responseObj = JSON.parse(response);
            console.log("HTTP===")
            console.log(responseObj)
            if(responseObj.code == 1){
                wsft.userData.uid = responseObj.uid
                this.uid = responseObj.uid
                this.sessionCode = responseObj.target.sessionCode
                this.loginTime = responseObj.loginTime
                var url = responseObj.target.URL
                cc.sys.localStorage.setItem("sessionCode",this.sessionCode);
                // url = config.socketUrl
                wsft._socket.connect(url)
            }else{
                // //sdk登录失败 重新掉起微信登录
                // var sessionCode = cc.sys.localStorage.getItem("sessionCode");
                // if(sessionCode != null){
                //     //微信登录
                // }else{
                //     //sessionCode登录
                //     // cc.sys.localStorage.removeItem("sessionCode") //移除"sessionCode"

                // }
                wsft.loading.hide()
                wsft.uiFuncs.showTip('重新掉起微信');
            }
        }else{
            wsft.loading.hide()
            wsft.uiFuncs.showTip('登录连接异常：' + status);
        }
    },
    login:function(){
        if(config.isDebug){
            var edit = cc.find("Canvas/debugLogin/editBox").getComponent(cc.EditBox);
            var _name = edit.string
            var _password = edit.string
            cc.log("_name=="+_name)
            if(_name ==""){
                wsft.uiFuncs.showTip("请输入账号",30)
                wsft.loading.hide()
            }else{
                wsft.sendDataHttp.Login_name_paw(_name,_password,this.loginCallback.bind(this))
            }
        }else{
            var url = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx11e205ff507dc176&redirect_uri=http://www.baidu.com&response_type=code&scope=SCOPE&state=STATE#wechat_redirect"
            wsft.https.get(url,this.loginCallback.bind(this))
            
            //sdk登录 token 登录
            
        }
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        var self = this
        if(name === "wx"){ 
            if (this.agreementCheckBox.isChecked) {
                wsft.loading.show("正在链接服务器...")
                // self.jumpScene()
                this.login()
            } 
            else {
                wsft.message.sendMessage(wsft.protocol.TCP_ONOPEN,"")
                wsft.uiFuncs.showTip('请同意用户使用协议请同意用户使用协议请同意用户使用协议',30)
            }
        }
        else if(name === "agreement"){
            if (!this.agreementLayer) {
                this.agreementLayer = cc.instantiate(this.agreementLayerPrafab);
                this.agreementLayer.setName("agreementLayer");
                this.node.addChild(this.agreementLayer);
            }else{
                this.agreementLayer.active = true;
            } 
        }
    },
    // 初始化所有的btn
    initAllBtn:function(){
        var self = this
        var btnHader = function(btnPath,name){
           var btn = cc.find(btnPath)
           wsft.event.addBtnEvent(btn,self.node,'loginScene','allBtnCallback',name);
        }
        btnHader('Canvas/wx_btn','wx'); //微信
        btnHader('Canvas/agreement_btn','agreement'); //协议

    },
    //跳转场景
    jumpScene:function(){
        wsft.loading.show("正在加载资源，请稍候...") 
        cc.director.loadScene('MainScene')
    }
    // update (dt) {},
});
