// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var gameChild = this.node.getChildByName("game_node");
        var myself = gameChild.getChildByName("myself_node");
        var pengangroot = myself.getChildByName("peng_gang_node");
        var realwidth = cc.winSize.width;
        pengangroot.scaleX = 1.2;
        pengangroot.scaleY = 1.2;
        var scale = realwidth / 1280;
        pengangroot.scaleX *= scale;
        pengangroot.scaleY *= scale;

        this.onGameBein()
        this.addMessgetListen()

    },
    start () {

    },
    addMessgetListen:function(){
        // wsft.message.addMessage(wsft.protocol.PUSHSENDCARD,this.pushSendCardpg.bind(this))//发牌
        // wsft.message.addMessage(wsft.protocol.RECEIVEPENGCARD,this.receivePengCardpg.bind(this))//碰牌(返回)
        wsft.message.addMessage(wsft.protocol.PUSHPENGCARD,this.pushPengCardpg.bind(this))//碰牌(推送)

        wsft.message.addMessage(wsft.protocol.RECEIVEANGANG,this.receivePengCardpg.bind(this))//暗杠(返回)
        wsft.message.addMessage(wsft.protocol.PUSHANGANG,this.pushPengCardpg.bind(this))//暗杠(推送)
        // wsft.message.addMessage(wsft.protocol.RECEIVEMINGGANG,this.receivePengCardpg.bind(this))//明杠(返回)
        wsft.message.addMessage(wsft.protocol.PUSHMINGGANG,this.pushPengCardpg.bind(this))//明杠(推送)
        // wsft.message.addMessage(wsft.protocol.RECEIVEJIEGANG,this.receivePengCardpg.bind(this))//接杠(返回)
        wsft.message.addMessage(wsft.protocol.PUSHJIEGANG,this.pushPengCardpg.bind(this))//接杠(推送)
        wsft.message.addMessage(wsft.protocol.PUSHROUNDRESULT,this.pushRoundResultpg.bind(this))//牌局结果 小结算
        wsft.message.addMessage(wsft.protocol.RECEIVEOFFLINERECONNECTION,this.receiveOfflineReconnectionpg.bind(this))//断线重连
        
    },
    pushRoundResultpg:function(data){//牌局结果 小结算
        var self = this;
        if(data.reconnect){
            self.onGameBein()
        }else{
            setTimeout(function(){
                self.onGameBein() 
            },3000)
        }
    },
    receiveOfflineReconnectionpg:function(){////断线重连
        var seats = wsft.gameData.seats;
        for(var i in seats){
            this.onPengGangChanged(seats[i]);
        }
    },
    receivePengCardpg:function(data){//碰牌(返回)
        var index = wsft.gameData.mySeatIndex - 1;
        var seats = wsft.gameData.seats;
        this.onPengGangChanged(seats[index])
    },
    pushPengCardpg:function(data){//碰牌(推送)
        var index = data.seetIndex - 1;
        var seats = wsft.gameData.seats;
        this.onPengGangChanged(seats[index])
    },
    pushSendCardpg:function(data){//发牌(推送)
        
    },
    onGameBein:function(){
        var sides = ['myself_node','right_node','up_node','left_node']
        for(var i = 0; i < sides.length;++i){
            var side = sides[i]
            console.log("onGameBein=="+side)
            this.hidePengGang(side);
        }
    },
    hidePengGang:function(side){
        var gameChild = this.node.getChildByName("game_node");
        var myself = gameChild.getChildByName(side);
        var pengangroot = myself.getChildByName("peng_gang_node");
        if(pengangroot){
            for(var i = 0; i < pengangroot.childrenCount; ++i){
                console.log("pengangroot=childrenCount=="+i)
                pengangroot.children[i].active = false;
                // pengangroot.removeChild(pengangroot.children[i]);
            }          
        }
    },
    onPengGangChanged:function(seatData){
        if(seatData.pengs == null && seatData.anGangs == null && seatData.mingGangs == null && seatData.jieGangs == null){
            return;
        }
        var localIndex = wsft.gameData.getLocalIndex(seatData.seetIndex);
        var side = wsft.mjCard.getSide(localIndex);
        var pre = wsft.mjCard.getOutPre(localIndex);
       
        // console.log("onPengGangChanged" + localIndex);
            
        var gameChild = this.node.getChildByName("game_node");
        var myself = gameChild.getChildByName(side);
        var pengangroot = myself.getChildByName("peng_gang_node");
        
        for(var i = 0; i < pengangroot.childrenCount; ++i){
            pengangroot.children[i].active = false;
            // pengangroot.removeChild(pengangroot.children[i]);
        }
        //初始化杠牌
        var index = 0;
        
        var gangs = seatData.anGangs
        for(var i = 0; i < gangs.length; ++i){
            var _mjNum = gangs[i].num
            var _mjType = gangs[i].type
            // var mjid = gangs[i];
            this.initPengAndGangs(pengangroot,side,pre,index,_mjNum,_mjType,"anGangs");
            index++;    
        } 
        var gangs = seatData.mingGangs
        for(var i = 0; i < gangs.length; ++i){
            var _mjNum = gangs[i].num
            var _mjType = gangs[i].type
            this.initPengAndGangs(pengangroot,side,pre,index,_mjNum,_mjType,"mingGangs");
            index++;    
        }
        
        var gangs = seatData.jieGangs
        for(var i = 0; i < gangs.length; ++i){
            var _mjNum = gangs[i].num
            var _mjType = gangs[i].type
            this.initPengAndGangs(pengangroot,side,pre,index,_mjNum,_mjType,"jieGangs");
            index++;    
        }
        
        //初始化碰牌
        var pengs = seatData.pengs
        if(pengs){
            for(var i = 0; i < pengs.length; ++i){
                // var mjid = pengs[i];
                var _mjNum = pengs[i].num
                var _mjType = pengs[i].type
                this.initPengAndGangs(pengangroot,side,pre,index,_mjNum,_mjType,"pengs");
                index++;    
            }    
        }        
    },
    initPengAndGangs:function(pengangroot,side,pre,index,mjNum,mjType,flag){
        var pgroot = null;
        
        if(pengangroot.childrenCount <= index){
            if(side == "left_node" || side == "right_node"){
                pgroot = cc.instantiate(wsft.mjCard.pengGangLeftRightPre);
            }
            else{
                pgroot = cc.instantiate(wsft.mjCard.pengGangMyUpPre);
            }
            
            pengangroot.addChild(pgroot);    
        }
        else{
            pgroot = pengangroot.children[index];
            pgroot.active = true;
        }
        // 'myself_node','right_node','up_node','left_node'
        if(side == "left_node"){
            pgroot.y = -(index * 25 * 3 + index*5);                
        }
        else if(side == "right_node"){
            pgroot.y = (index * 25 * 3 + index*5);
            pgroot.zIndex = -index;
        }
        else if(side == "myself_node"){
            pgroot.x = index * 55 * 3 + index * 10;     
        }
        else{
            pgroot.x = -(index * 55*3 + index * 10);
        }

        var sprites = pgroot.getComponentsInChildren(cc.Sprite);
        for(var s = 0; s < sprites.length; ++s){
            var sprite = sprites[s];
            if(sprite.node.name == "gang"){
                var isGang = flag != "pengs";
                sprite.node.active = isGang;
                sprite.node.scaleX = 1.0;
                sprite.node.scaleY = 1.0;
                if(flag == "anGangs"){
                    // sprite.spriteFrame = wsft.mjCard.getEmptySpriteFrame(side);
                    sprite.spriteFrame = wsft.mjCard.getSpriteFrameByMJ(pre,mjNum,mjType); 
                    // if(side == "myself_node" || side == "up_node"){
                    //     sprite.node.scaleX = 1.4;
                    //     sprite.node.scaleY = 1.4;               
                    // }
                }   
                else{
                    sprite.spriteFrame = wsft.mjCard.getSpriteFrameByMJ(pre,mjNum,mjType);    
                }
            }
            else{
                if(flag == "anGangs"){
                    sprite.spriteFrame = wsft.mjCard.getEmptySpriteFrame(side);
                }else{
                    sprite.spriteFrame = wsft.mjCard.getSpriteFrameByMJ(pre,mjNum,mjType);
                }
            }
        }
    }

    // update (dt) {},
});
