// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        nameTable:[],
        time_label:null,
        _time:-1,
    },
    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        for(var i = 0; i < 4; ++i){
            this.nameTable.push(this.node.getChildByName("name_label_"+i))
            this.nameTable[i].getChildByName("label").getComponent(cc.Label).string = "等待选择"
            
        }
        this.initAllBtn()
        this.time_label = this.node.getChildByName("time_label").getComponent(cc.Label)
        this.time_label.string = "0"

    },
    start () {
    },

    initName:function(){
        var seats = wsft.gameData.seats
        
        for(var i = 0; i < seats.length; ++i){
            this.nameTable[i].getComponent(cc.Label).string = seats[i].nickname
            this.nameTable[i].getChildByName("label").getComponent(cc.Label).string = "等待选择"
            this.nameTable[i].active = true
        }
        if(wsft.gameData.playerNum  == 4){
            this.nameTable[0].y = 65
            this.nameTable[1].y = 25
            this.nameTable[2].y = -15
            this.nameTable[3].y = -55
        }else if(wsft.gameData.playerNum  == 3){
            this.nameTable[0].y = 60
            this.nameTable[1].y = 0
            this.nameTable[2].y = -60
            this.nameTable[3].active = false;
        }else if(wsft.gameData.playerNum  == 2){
            this.nameTable[0].y = 40
            this.nameTable[1].y = -40
            this.nameTable[2].active = false;
            this.nameTable[3].active = false;
        }
    },
    refreshDada:function(data){
        // optional int32 isOk = 1;//是否解散成功 0等待 1成功 2失败
        // repeated int32 agreeSeetIndex = 2;//同意解散
        // repeated int32 disagreeSeetIndex = 3;//拒绝解散
        // optional int64 memberDisInMillis = 4;//解散剩余（毫秒）
        // optional int32 ncont = 5;
        this.showBtn()
        for(var i = 0; i < data.agreeSeetIndex.length; ++i){
            var index = data.agreeSeetIndex[i] - 1;
            // this.nameTable[index].string = seats[index].nickname
            if(i == 0){
                this.nameTable[index].getChildByName("label").getComponent(cc.Label).string = "申请解散房间,是否同意？"
            }else{
                this.nameTable[index].getChildByName("label").getComponent(cc.Label).string = "同意"
            }
            if(wsft.gameData.mySeatIndex == data.agreeSeetIndex[i]){
                console.log("data.agreeSeetIndex[i]data.agreeSeetIndex[i]==",data.agreeSeetIndex[i])
                this.hideBtn()
            }
        }
        for(var i = 0; i < data.disagreeSeetIndex.length; ++i){
            var index = data.disagreeSeetIndex[i] - 1;
            // this.nameTable[index].string = seats[index].nickname
            this.nameTable[index].getChildByName("label").getComponent(cc.Label).string = "拒绝"
            if(wsft.gameData.mySeatIndex == data.disagreeSeetIndex[i]){
                console.log("data.agreeSeetIndex[i]data.disagreeSeetIndex[i]==",data.disagreeSeetIndex[i])
                this.hideBtn()
            }
        }
        this._time = Math.ceil((data.memberDisInMillis)/1000)
        
        // this.time_label.string =
    },
    hideBtn:function(){
        var confirm = this.node.getChildByName("confirm_btn")
        var reject = this.node.getChildByName("reject_btn")
        confirm.active = false
        reject.active = false
    },
    showBtn:function(){
        var confirm = this.node.getChildByName("confirm_btn")
        var reject = this.node.getChildByName("reject_btn")
        confirm.active = true
        reject.active = true
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name == 'confirm'){
            wsft.sendDataSocket.ApplayDissolveRoom(true)
        }
        else if(name == 'reject'){
            wsft.sendDataSocket.ApplayDissolveRoom(false)
        }
        else{
            
        }

    },
    initAllBtn:function(){
        var self = this
        var btnHader = function(btn,name){
            // var btn = cc.find(btnPath);
            wsft.event.addBtnEvent(btn,self.node,'dissolveRoomLayer','allBtnCallback',name);
        }
        btnHader(this.node.getChildByName("confirm_btn"),'confirm'); //确定
        btnHader(this.node.getChildByName("reject_btn"),'reject'); //拒绝
    },
    update (dt) {
        if(this._time > 0){
            this._time -= dt;
            // var pre = "";
            if(this._time < 0){
                this._time = 0;
            }
            var t = Math.ceil(this._time);//返回大于等于参数的最小整数
            // if(t < 10){
            //     pre = "0";
            // }
            this.time_label.string = t; 
            // this._timeLabel.string = pre + t; 
        }
    },
});
