window.wsft = window.wsft || {}
var _socket = {
    url:"",
    webSocker:null,
    nodeName:"websocket",
    //链接
    connect:function(_url){
        this.url = _url;
        this.reconnect();
    },
    reconnect:function(){
        if(!this.url){
            return;
        }
        var that = this;
        this.killConnect();
        var ws = new WebSocket(this.url);
        cc.log(ws)
        this.webSocker = ws;
        ws.onopen = function (event) {
            cc.log("Send data WS was opened.");
            // var node = cc.director.getScene().getChildByName(that.nodeName);
            // if(node){
            //     node.destroy();
            // }
            //连接成功发送的第一条数据
            wsft.message.sendMessage(wsft.protocol.TCP_ONOPEN); //发送监听
        };
        ws.onmessage = function (event) {
            // console.log(event)
            if(!event){
                return;
            }
            wsft.message.sendMessage(wsft.protocol.MSG_HEART_BEAT,"");
            if(cc.sys.isBrowser){//判断是否是浏览器
                var reader = new FileReader();
                reader.readAsArrayBuffer(event.data);
                // console.log(event.data)
                reader.onload = function (e) {
                    that.decodePacket(reader.result)
                }
            }else{
                that.decodePacket(event.data)
            }
           
            
        };
         // 连接出现问题时的回调
         ws.onerror = function (event) {
            cc.log("Send Text fired an error");
            wsft.heartBeat.HearTime = 0
            wsft.uiFuncs.showTip("无法连接服务器，请检查网络。");
            wsft.loading.hide()
        };
        // 连接关闭之后的回调
        ws.onclose = function (event) {
            cc.log("WebSocket instance closed.");
            wsft.heartBeat.HearTime = 0
            //断线重连
            
            
        };

    },

    //断开链接
    killConnect:function(){
        if(this.webSocker && this.webSocker.readyState == WebSocket.OPEN){
            this.webSocker.close();
            this.webSocker = null;
        } 
    },
    // 接收到消息之后的回调
    // 发送消息
    sendMsg:function(cmd,msg){
        //连接不可用
        if (!this.isConnectState()){
            return;
        } 
        var _length = 0
        if(msg){
            _length = msg.byteLength
        }
        // console.log(_length)
        var buf = new ArrayBuffer(4+_length);
        var dv =new DataView(buf);
        dv.setUint16(0,2+_length,false);
        dv.setUint16(2,cmd,false);
        if(msg){
            var dvv =new DataView(msg);
            for(var i = 0; i < _length; i++){
                dv.setUint8(i + 4,dvv.getUint8(i),false);
            }
        }
        // console.log(buf)
        // console.log(dv)
        console.log("发送Socket==>>"+cmd);
        
        this.webSocker.send(buf);
    },

    //连接状态
    isConnectState:function(){
        return this.webSocker && this.webSocker.readyState == WebSocket.OPEN;
    },

    /**
     * 据结构解析
     */
    decodePacket:function(data) {
        var buf = new Uint8Array(data);
        // var buf = new DataView(reader.result);
        var dataView = new DataView(buf.buffer);
        var _length = dataView.getInt16(0)
        var _msgId = dataView.getInt16(2)
        // console.log("msg===",data)
        // console.log("接受====>>"+_msgId)
        if(_msgId == -1 ){
            return
        }
        console.log("接受====>>"+_msgId)
        if (!Uint8Array.prototype.slice) {  //引擎或浏览器中未带有 slice 方法 进行强制加入
            Object.defineProperty(Uint8Array.prototype, 'slice', {
                value: Array.prototype.slice
            });
        }
        // console.log(buf.buffer)
        // console.log(buf.buffer.getUint16(2))
        // console.log("接受====>>"+buf[3])
        // console.log("接受====>>"+_msgId)
        var buffer = buf.slice(4,_length+2);
        var date =  wsft.pbMsg.decodePB(_msgId,buffer)
        wsft.message.sendMessage(_msgId,date);
    },

    /**
     * 切割数据
     */
    sliceBuffer:function(arraybuffer, start, end) {
        var bytes = arraybuffer.byteLength;
        start = start || 0;
        end = end || bytes;

        if (arraybuffer.slice) {
            return arraybuffer.slice(start, end);
        }

        if (start < 0) {
            start += bytes;
        }
        if (end < 0) {
            end += bytes;
        }
        if (end > bytes) {
            end = bytes;
        }

        if (start >= bytes || start >= end || bytes === 0) {
            return new ArrayBuffer(0);
        }

        var abv = new Uint8Array(arraybuffer);
        var result = new Uint8Array(end - start);
        for (var i = start, ii = 0; i < end; i++ , ii++) {
            result[ii] = abv[i];
        }
        return result.buffer;
    }
        
}
wsft._socket = module.exports = _socket;