// var _ChiFengMJ = require("ChiFengMJ")
var RULE_NAME = ["ChiFengMJ","DouDiZhu"]
var RULE = 0
cc.Class({
    extends: cc.Component,

    properties: {
        contentBtn:cc.Node,
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        this.initAllBtn()
        // this.btn_1 = cc.find('Canvas/create_room_layer/btn_scroll_view/view/content/btn_1').getComponent(cc.Button);
        // this.btn_2 = cc.find('Canvas/create_room_layer/btn_scroll_view/view/content/btn_2').getComponent(cc.Button);
        this.create_rule_node_1 = this.node.getChildByName("create_rule_node_1");
        // this.create_rule_node_2 = cc.find('Canvas/create_room_layer/create_rule_node_2')
        // this.model_node = cc.find('Canvas/create_room_layer/create_rule_node_1/model_node').getComponent(cc.ToggleContainer)
        // console.log(this.model_node.toggleItems)
        var create_bg = this.node.getChildByName("create_bg");
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,create_bg,callback);
        
    },
    start () {
        
        this.ChiFengMJ  = this.create_rule_node_1.getComponent("ChiFengMJ")

        var ruleData = JSON.parse(cc.sys.localStorage.getItem('ruleData'));
        if(ruleData != null){
            console.log(ruleData)
            var name = ruleData.name
            var _chekedTable = ruleData.chekedTable
            // this.musicVolume = parseFloat(m);//将string转化为float
            if(name == RULE_NAME[0]){
                this.ChiFengMJ.setLabelColor(_chekedTable)
                if(!this.create_rule_node_1.activeInHierarchy){
                    this.create_rule_node_1.active = true
                }
            }
        }else{
             this.ChiFengMJ.initColor()
        }

        this.ChiFengMJ.initToggleLabel()
        
    },
    onEnable(){
        wsft.loading.hide()
        // this.btn_1.interactable = false;
        // this.btn_2.interactable = true;
        // this.create_rule_node_1.active = true;
        // this.create_rule_node_2.active = false;
    },
    jumpScene:function(){
        wsft.loading.show("正在加载资源，请稍候...") 
        cc.director.loadScene('GameScene')
    },
    createRoom:function(){
        var data = {}
        data.piaohu = false;
        data.shisanyao = false;
        data.qiDui = false;
        data.qingYiSe  = false;
        data.shisanyao = false;
        data.diangang = false;
        data.area = 4;// 玩法类型(赤峰玩法)
        data.timesLimit =3;//倍数（）暂时要用
        if( RULE == 0){
            data.area = 4;//什么麻将 1赤峰
        }
        var _chekedTable = this.ChiFengMJ.judgeChecked()
        for(var i = 0;i<_chekedTable.length;++i){
            if( _chekedTable[i] == 3 ){//房主
                data.zhifu = true
            }
            if( _chekedTable[i] == 4 ){//aa
                data.zhifu = false
            }
            if( _chekedTable[i] ==5 ){//2人
                data.num = 2
            }
            if( _chekedTable[i] ==6 ){//3人
                data.num = 3
            }
            if( _chekedTable[i] ==7 ){//4人
                data.num = 4
            }
            if( _chekedTable[i] ==8 ){//七小对
                data.qiDui = true;
            }
            if( _chekedTable[i] ==9 ){//飘胡
                data.piaohu = true;
            }
            if( _chekedTable[i] ==10 ){//十三幺
                data.shisanyao = true;
            }
            if( _chekedTable[i] ==11 ){//杠上炮
                data.gangshangpao  = true;
            }
            if( _chekedTable[i] ==12 ){//清一色
                data.qingYiSe  = true;
            }
            if( _chekedTable[i] ==13 ){//点杠
                data.diangang  = true;
            }
        }
        data.bankerMode = _chekedTable[0]+1//局
        console.log(data)
        wsft.loading.show("正在创建房间...")
        wsft.sendDataSocket.CreateRoomCm(data)
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close"){
            this.node.active = false;
        }
        else if(name == 'create'){
            cc.log("创建房间")
            this.createRoom()
            // this.jumpScene()
        }else if(name == 'btn_1'){
            RULE = 0
            // this.btn_1.interactable = false;
            // this.btn_2.interactable = true;
            // this.create_rule_node_1.active = true
            // this.create_rule_node_2.active = false

        }
        else if(name == 'btn_2'){
            RULE = 1
            // this.btn_1.interactable = true;
            // this.btn_2.interactable = false;
            // this.create_rule_node_1.active = false
            // this.create_rule_node_2.active = true
        }
        else{
            
        }

    },
    initAllBtn:function(){
        var self = this
        var btnHader = function(btn,name){
            // var btn = cc.find(btnPath);
            wsft.event.addBtnEvent(btn,self.node,'createLayer','allBtnCallback',name);
        }
        btnHader(this.node.getChildByName("close_btn"),'close'); //close
        
        btnHader(this.contentBtn.getChildByName("btn_1"),'btn_1'); //btn_1
        // btnHader('Canvas/create_room_layer/btn_scroll_view/view/content/btn_2','btn_2'); //btn_2
        btnHader(this.node.getChildByName("create_roon_btn"),'create'); //创建房间

        
    },
    // update (dt) {},
});
