//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        _arrow:null,
        _pointer:null,
        _pointerChildren:[],
        _timeLabel:null,
        _time:-1,
        _alertTime:-1,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var gameChild = this.node.getChildByName("game_node");
        this._arrow = gameChild.getChildByName("arrow_node");
        this._pointer = this._arrow.getChildByName("pointer_node");
        this._pointer.rotation = 90 * (wsft.gameData.mySeatIndex -1)
        if(wsft.gameData.playerNum  == 2){
            if(wsft.gameData.mySeatIndex == 2){
                this._pointer.rotation = 90 * 2
            }
        }
        this._arrow.active = true
        for(var i = 0; i<4;++i){    
            this._pointerChildren.push(this._pointer.getChildByName("arrow_"+i))
        }
        this.initPointer();
        this._timeLabel = this._arrow.getChildByName("count_down_label").getComponent(cc.Label);
        this._timeLabel.string = "00";

        this.mjCount = this._arrow.getChildByName("mj_count").getComponent(cc.Label)
        this.mjCount.string = "0张"
        this.juCount = this._arrow.getChildByName("ju_count").getComponent(cc.Label)
        this.juCount.string = "0/0圈"

        this.addMessgetListen()
    },

    start () {

    },
    addMessgetListen:function(){
        wsft.message.addMessage(wsft.protocol.CATCHCARD,this.catchCarda.bind(this))//摸牌(自己)
        wsft.message.addMessage(wsft.protocol.SEATCATCHCARD,this.seatCatchCarda.bind(this))//谁摸牌(推送)

        // wsft.message.addMessage(wsft.protocol.RECEIVEPENGCARD,this.seatCatchCarda.bind(this))//碰牌(返回)
        wsft.message.addMessage(wsft.protocol.PUSHPENGCARD,this.seatCatchCarda.bind(this))//碰牌(推送)
        
        wsft.message.addMessage(wsft.protocol.RECEIVEANGANG,this.seatCatchCarda.bind(this))//暗杠(返回)
        wsft.message.addMessage(wsft.protocol.PUSHANGANG,this.seatCatchCarda.bind(this))//暗杠(推送)
        // wsft.message.addMessage(wsft.protocol.RECEIVEMINGGANG,this.seatCatchCarda.bind(this))//明杠(返回)
        wsft.message.addMessage(wsft.protocol.PUSHMINGGANG,this.seatCatchCarda.bind(this))//明杠(推送)
        // wsft.message.addMessage(wsft.protocol.RECEIVEJIEGANG,this.seatCatchCarda.bind(this))//接杠(返回)
        wsft.message.addMessage(wsft.protocol.PUSHJIEGANG,this.seatCatchCarda.bind(this))//接杠(推送)

        wsft.message.addMessage(wsft.protocol.PUSHOUTCARD,this.seatCatchCarda.bind(this))//出牌(推送)(断线重连)

        wsft.message.addMessage(wsft.protocol.RECEIVEOFFLINERECONNECTION,this.receiveOfflineReconnectiona.bind(this))//断线重连
    },
    receiveOfflineReconnectiona:function(data){
        if(data.start){
            this.catchCarda()
        }
    },
    catchCarda:function(data){
        this._arrow.active = true
        this.mjCount.string = wsft.gameData.cardNum+"张"
        this.initPointer()
        this._time=15;
        this._alertTime=3;
    },
    seatCatchCarda:function(data){
        this.mjCount.string = wsft.gameData.cardNum+"张"
        this._arrow.active = true
        this.initPointer()
        this._time=15;
        this._alertTime=3;
    },
    initPointer:function(){
        if(!this._arrow.active){
            return;
        }
        if(wsft.gameData.turnIndex == -1){
            return;
        }
        var turn = wsft.gameData.turnIndex;
        // var localIndex = wsft.gameData.getLocalIndex(turn);
        var localIndex = turn - 1;
        if(wsft.gameData.playerNum  == 3){
            if(wsft.gameData.mySeatIndex == 1){
                if(localIndex == 2){
                    localIndex = 3
                }
            }
            if(wsft.gameData.mySeatIndex == 2){
                if(localIndex == 3){
                    localIndex = 2
                }
            }
            if(wsft.gameData.mySeatIndex == 3){
                if(localIndex == 0){
                    localIndex = 3
                }
            }
        }else if(wsft.gameData.playerNum  == 2){
            if(localIndex == 1){
                localIndex = 2
            }
        }
        for(var i = 0; i < 4; ++i){
            this._pointerChildren[i].active = i == localIndex;
        }

    },
    update (dt) {
        if(this._time > 0){
            this._time -= dt;
            if(this._alertTime > 0 && this._time < this._alertTime){
                //播放音效
                this._alertTime = -1;
            }
            var pre = "";
            if(this._time < 0){
                this._time = 0;
            }
            var t = Math.ceil(this._time);//返回大于等于参数的最小整数
            if(t < 10){
                pre = "0";
            }
            this._timeLabel.string = pre + t; 
        }
    },
});
